.profile {
    grid-area: content;
}
.bg {
    width: 100%;
    height: 300px;
    object-fit: cover;
}
.profileInfo {
    display: grid;
    grid-template-areas: 'image info';
    grid-template-columns: 2fr 10fr;
    grid-gap: 5px;
}
.avatar {
    grid-area: image;
    width: 200px;
    height: auto;
}
.aboutMe {
    grid-area: info;
    background-color: #ddc352;
    padding: 15px;
}